/**
 * How to apply for postgraduate taught degrees
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/how-to-apply/how-to-apply--pgt
 * @author Web Development
 * @copyright City, University of London 2020
 */

import React, { useEffect, useState } from 'react';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

import PropTypes from 'prop-types';
import createFocusTrap from 'focus-trap';

const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
];

/**
 * Launch the how to apply modal.
 *
 * @param {object} props The JSON configuration file for the modal.
 * @return {object} The React component to render.
 */
function HowToApply(props) {
    let configArray = props.config,
        filteredQuals,
        subjectQuestion,
        entryQuestion;

    const entryPoints = distilConfiguration(props.config),
        initialPreferences = {
            // If single qualification, set this as selected qualification, else set to null
            qualification:
                Object.keys(entryPoints).length === 1
                    ? Object.keys(entryPoints[0])[0]
                    : null,
            subject: null,
            entry: null,
            location: null,
        },
        [modalVisible, setModalVisible] = useState(false),
        [preferences, setPreferences] = useState(initialPreferences),
        [question, setQuestion] = useState(
            nextQuestion(preferences, entryPoints)
        ),
        [multipleSubjects, setMultipleSubjects] = useState(true),
        [multipleEntryPoints, setMultipleEntryPoints] = useState(true),
        [multipleLocations, setMultipleLocations] = useState(true),
        qualificationQuestion = question === 'qualification' && (
            <>
                <div className="how-to-apply--pgt--js__modal__content-wrapper">
                    <p>Choose the qualification you wish to apply for:</p>
                </div>
                <ul className="how-to-apply--pgt--js__options">
                    {entryPoints.map((qualification, i) => (
                        <li key={'qualification' + i}>
                            <button
                                onClick={() => {
                                    let newPreferences = preferences;
                                    newPreferences.qualification = Object.keys(
                                        qualification
                                    )[0];

                                    const subjectOptions =
                                        qualification[
                                            newPreferences.qualification
                                        ];
                                    // console.log(newPreferences.qualification);

                                    // No multiple subject/route; skip this window and check entry points
                                    if (subjectOptions.length === 1) {
                                        newPreferences.subject = 'UNIQUE';

                                        filteredQuals = configArray.filter(
                                            (c) =>
                                                c.header ===
                                                newPreferences.qualification
                                        );

                                        // Calculate number of entry points (dates)
                                        let filteredQualsOptions =
                                            filteredQuals[0]['options'][0][
                                                'options'
                                            ];

                                        if (filteredQualsOptions.length === 1) {
                                            // One date, skip this step
                                            // setEntryDisplay(false);
                                            // setApplyDisplay(true);
                                        }
                                        setMultipleSubjects(false);

                                        filteredQuals = configArray.filter(
                                            (c) =>
                                                c.header ===
                                                newPreferences.qualification
                                        );

                                        // Calculate number of entry points (dates)
                                        let qualEntryPoints =
                                            filteredQuals[0]['options'][0][
                                                'options'
                                            ].length;
                                        if (qualEntryPoints === 1) {
                                            // This closes the modal, ERR: Cannot read property 'UNIQUE' of undefined at nextLevel
                                            // newPreferences.entry = 'UNIQUE';
                                            setMultipleEntryPoints(false);
                                            subjectQuestion = false;
                                            entryQuestion = true;
                                        }
                                    } else {
                                        newPreferences.subject = null;
                                        setMultipleSubjects(true);
                                    }
                                    // console.log('entry points: ' + entryPoints);

                                    setPreferences(newPreferences);
                                    setQuestion(
                                        nextQuestion(
                                            newPreferences,
                                            entryPoints
                                        )
                                    );
                                }}
                            >
                                <span>{Object.keys(qualification)[0]}</span>
                            </button>
                        </li>
                    ))}
                </ul>
            </>
        );
    // console.log(question);
    subjectQuestion = question === 'subject' && (
        <>
            <div className="how-to-apply--pgt--js__modal__content-wrapper">
                <p>Choose the route you wish to apply for:</p>
            </div>
            <ul className="how-to-apply--pgt--js__options">
                {nextLevel(entryPoints, preferences.qualification).map(
                    (subject, i) => (
                        <li key={'subject' + i}>
                            <button
                                onClick={() => {
                                    const newPreferences = preferences;
                                    newPreferences.subject = Object.keys(
                                        subject
                                    )[0];

                                    const entryOptions =
                                        subject[newPreferences.subject];

                                    if (entryOptions.length === 1) {
                                        newPreferences.entry = Object.keys(
                                            entryOptions[0]
                                        )[0];
                                    } else {
                                        newPreferences.entry = null;
                                    }

                                    setPreferences(newPreferences);
                                    setQuestion(
                                        nextQuestion(
                                            newPreferences,
                                            entryPoints
                                        )
                                    );
                                }}
                            >
                                <span>{Object.keys(subject)[0]}</span>
                            </button>
                        </li>
                    )
                )}
            </ul>
        </>
    );
    let qual = preferences.qualification ? preferences.qualification : null;
    let eFilteredDates;

    // If single qualification exists (or selected)
    if (qual) {
        // Check entry dates for selected qualification against original config data
        let eFilteredQuals = configArray.filter((c) => c.header === qual);
        eFilteredDates = eFilteredQuals[0]['options'][0]['options'];
    }
    if (eFilteredDates) {
        if (eFilteredDates.length > 1) {
            entryQuestion = question === 'entry' && (
                <>
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        <p>Choose the entry point you wish to apply for:</p>
                    </div>
                    <ul className="how-to-apply--pgt--js__options">
                        {nextLevel(
                            nextLevel(entryPoints, preferences.qualification),
                            preferences.subject
                        ).map((entryPoint, i) => {
                            const formattedDate = new Date(
                                Object.keys(entryPoint)[0]
                            );

                            return (
                                <li key={'entry-point' + i}>
                                    <button
                                        onClick={() => {
                                            const newPreferences = preferences;
                                            newPreferences.entry = Object.keys(
                                                entryPoint
                                            )[0];

                                            const locationOptions =
                                                entryPoint[
                                                    newPreferences.entry
                                                ];

                                            if (locationOptions.length === 1) {
                                                newPreferences.location = Object.keys(
                                                    locationOptions[0]
                                                )[0];
                                                setMultipleLocations(false);
                                            } else {
                                                newPreferences.location = null;
                                                setMultipleLocations(true);
                                            }

                                            setPreferences(newPreferences);
                                            setQuestion(
                                                nextQuestion(
                                                    newPreferences,
                                                    entryPoints
                                                )
                                            );
                                        }}
                                    >
                                        <span>
                                            {
                                                months[
                                                    formattedDate.getUTCMonth()
                                                ]
                                            }{' '}
                                            {formattedDate.getUTCFullYear()}
                                        </span>
                                    </button>
                                </li>
                            );
                        })}
                    </ul>
                </>
            );
        } else {
            entryQuestion = false;
        }
    }

    const locationQuestion = question === 'location' && (
        <>
            <div className="how-to-apply--pgt--js__modal__content-wrapper">
                <p>Choose where you wish to study:</p>
            </div>
            <ul className="how-to-apply--pgt--js__options">
                {nextLevel(
                    nextLevel(
                        nextLevel(entryPoints, preferences.qualification),
                        preferences.subject
                    ),
                    preferences.entry
                ).map((location, i) => (
                    <li key={'location' + i}>
                        <button
                            onClick={() => {
                                const newPreferences = preferences;
                                newPreferences.entry = Object.keys(location)[0];

                                setPreferences(newPreferences);
                                setQuestion(
                                    nextQuestion(newPreferences, entryPoints)
                                );
                            }}
                        >
                            <span>{Object.keys(location)[0]}</span>
                        </button>
                    </li>
                ))}
            </ul>
        </>
    );
    let applyQuestion = question === 'apply' && (
        <>
            <div className="how-to-apply--pgt--js__modal__content-wrapper">
                <p>Apply online now:</p>
            </div>
            <ul className="how-to-apply--pgt--js__options">
                {nextLevel(
                    nextLevel(
                        nextLevel(
                            nextLevel(entryPoints, preferences.qualification),
                            preferences.subject
                        ),
                        preferences.entry
                    ),
                    preferences.location
                ).map((method, i) => (
                    <li key={'method' + i}>
                        <a href={method[Object.keys(method)[0]]}>
                            <span>
                                {Object.keys(method)[0]}{' '}
                                <span
                                    className="far fa-external-link"
                                    aria-label="(external link)"
                                ></span>
                            </span>
                        </a>
                    </li>
                ))}
            </ul>
        </>
    );
    if (qual) {
        if (eFilteredDates.length === 1) {
            // console.log('load apply');
            const newPreferences = preferences;
            // newPreferences.entry = 'UNIQUE';
            // console.log(newPreferences);

            setPreferences(newPreferences);
            setQuestion(nextQuestion(newPreferences, entryPoints));
        }
    }

    let qualificationsProgress =
            Object.keys(entryPoints).length > 1 ? (
                question === 'qualification' ? (
                    <li className="how-to-apply--pgt--js__modal__progress__current">
                        <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                            <span className="how-to-apply--pgt--js__modal__progress__circle">
                                <span className="fas fa-circle icon"></span>
                            </span>
                            <span className="how-to-apply--pgt--js__modal__progress__text">
                                Qualification
                            </span>
                        </span>
                    </li>
                ) : preferences.qualification ? (
                    <li className="how-to-apply--pgt--js__modal__progress__previous">
                        <button
                            className="how-to-apply--pgt--js__modal__progress__wrapper"
                            onClick={() => {
                                const newPreferences = preferences;

                                newPreferences.qualification = null;
                                newPreferences.subject = null;
                                newPreferences.entry = null;
                                newPreferences.location = null;

                                setPreferences(newPreferences);
                                setQuestion(
                                    nextQuestion(newPreferences, entryPoints)
                                );
                            }}
                        >
                            <span className="how-to-apply--pgt--js__modal__progress__circle">
                                <span
                                    className="fas fa-check icon"
                                    aria-label="Back to choose qualification"
                                ></span>
                            </span>
                            <span className="how-to-apply--pgt--js__modal__progress__text">
                                {preferences.qualification}
                            </span>
                        </button>
                    </li>
                ) : null
            ) : null,
        subjectsProgress = multipleSubjects ? (
            question === 'subject' ? (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Route
                        </span>
                    </span>
                </li>
            ) : preferences.subject ? (
                <li className="how-to-apply--pgt--js__modal__progress__previous">
                    <button
                        className="how-to-apply--pgt--js__modal__progress__wrapper"
                        onClick={() => {
                            const newPreferences = preferences;

                            newPreferences.subject = null;
                            newPreferences.entry = null;
                            newPreferences.location = null;

                            setPreferences(newPreferences);
                            setQuestion(
                                nextQuestion(newPreferences, entryPoints)
                            );
                        }}
                    >
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className="fas fa-check icon"
                                aria-label="Back to choose study route"
                            ></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            {preferences.subject}
                        </span>
                    </button>
                </li>
            ) : null
        ) : null,
        entryPointsProgress = multipleEntryPoints ? (
            question === 'entry' ? (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Entry point
                        </span>
                    </span>
                </li>
            ) : preferences.entry ? (
                <li className="how-to-apply--pgt--js__modal__progress__previous">
                    <button
                        className="how-to-apply--pgt--js__modal__progress__wrapper"
                        onClick={() => {
                            const newPreferences = preferences;

                            newPreferences.entry = null;
                            newPreferences.location = null;

                            setPreferences(newPreferences);
                            setQuestion(
                                nextQuestion(newPreferences, entryPoints)
                            );
                        }}
                    >
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className="fas fa-check icon"
                                aria-label="Back to choose entry point"
                            ></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            {months[new Date(preferences.entry).getUTCMonth()]}{' '}
                            {new Date(preferences.entry).getUTCFullYear()}
                        </span>
                    </button>
                </li>
            ) : null
        ) : null,
        locationsProgress = multipleLocations ? (
            question === 'location' ? (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Location
                        </span>
                    </span>
                </li>
            ) : preferences.location ? (
                <li className="how-to-apply--pgt--js__modal__progress__previous">
                    <button
                        className="how-to-apply--pgt--js__modal__progress__wrapper"
                        onClick={() => {
                            const newPreferences = preferences;

                            newPreferences.location = null;

                            setPreferences(newPreferences);
                            setQuestion(
                                nextQuestion(newPreferences, entryPoints)
                            );
                        }}
                    >
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className="fas fa-check icon"
                                aria-label="Back to choose location"
                            ></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            {preferences.location}
                        </span>
                    </button>
                </li>
            ) : null
        ) : null,
        nextStep =
            question !== 'apply' ? (
                <li className="how-to-apply--pgt--js__modal__progress__next">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className="fas fa-circle icon"
                                aria-hidden="true"
                            ></span>
                        </span>
                    </span>
                </li>
            ) : (
                <li className="how-to-apply--pgt--js__modal__progress__current how-to-apply--pgt--js__modal__progress__last">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Method of study
                        </span>
                    </span>
                </li>
            ),
        qualificationsProgressMobile =
            Object.keys(entryPoints).length > 1 &&
            (question === 'qualification' || preferences.qualification) ? (
                <li
                    className={
                        question === 'qualification'
                            ? 'how-to-apply--pgt--js__modal__progress__current'
                            : 'how-to-apply--pgt--js__modal__progress__previous'
                    }
                >
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className={
                                    question === 'qualification'
                                        ? 'fas fa-circle icon'
                                        : 'fas fa-check icon'
                                }
                            ></span>
                        </span>
                        {question === 'qualification' && (
                            <span className="how-to-apply--pgt--js__modal__progress__text">
                                Qualification
                            </span>
                        )}
                    </span>
                </li>
            ) : null,
        subjectsProgressMobile =
            multipleSubjects &&
            (question === 'subject' || preferences.subject) ? (
                <li
                    className={
                        question === 'subject'
                            ? 'how-to-apply--pgt--js__modal__progress__current'
                            : 'how-to-apply--pgt--js__modal__progress__previous'
                    }
                >
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className={
                                    question === 'subject'
                                        ? 'fas fa-circle icon'
                                        : 'fas fa-check icon'
                                }
                            ></span>
                        </span>
                        {question === 'subject' && (
                            <span className="how-to-apply--pgt--js__modal__progress__text">
                                Subject
                            </span>
                        )}
                    </span>
                </li>
            ) : null,
        entryPointsProgressMobile =
            multipleEntryPoints &&
            (question === 'entry' || preferences.entry) ? (
                <li
                    className={
                        question === 'entry'
                            ? 'how-to-apply--pgt--js__modal__progress__current'
                            : 'how-to-apply--pgt--js__modal__progress__previous'
                    }
                >
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className={
                                    question === 'entry'
                                        ? 'fas fa-circle icon'
                                        : 'fas fa-check icon'
                                }
                            ></span>
                        </span>
                        {question === 'entry' && (
                            <span className="how-to-apply--pgt--js__modal__progress__text">
                                Entry point
                            </span>
                        )}
                    </span>
                </li>
            ) : null,
        locationsProgressMobile =
            multipleLocations &&
            (question === 'location' || preferences.location) ? (
                <li
                    className={
                        question === 'location'
                            ? 'how-to-apply--pgt--js__modal__progress__current'
                            : 'how-to-apply--pgt--js__modal__progress__previous'
                    }
                >
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span
                                className={
                                    question === 'location'
                                        ? 'fas fa-circle icon'
                                        : 'fas fa-check icon'
                                }
                            ></span>
                        </span>
                        {question === 'location' && (
                            <span className="how-to-apply--pgt--js__modal__progress__text">
                                Location
                            </span>
                        )}
                    </span>
                </li>
            ) : null,
        nextStepMobile =
            question !== 'apply' ? (
                <li className="how-to-apply--pgt--js__modal__progress__next">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                    </span>
                </li>
            ) : (
                <li className="how-to-apply--pgt--js__modal__progress__current how-to-apply--pgt--js__modal__progress__last">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Method of study
                        </span>
                    </span>
                </li>
            ),
        alternative = props.element.dataset.otheryear &&
            props.element.dataset.otherurl && (
                <p className="how-to-apply--pgt--js__modal__alternative">
                    <a href={props.element.dataset.otherurl}>
                        {props.element.dataset.otheryear} entry is also
                        available.
                    </a>
                </p>
            ),
        reset = JSON.stringify(preferences) !==
            JSON.stringify(initialPreferences) && (
            <div className="how-to-apply--pgt--js__modal__reset">
                <button
                    onClick={() => {
                        setPreferences(initialPreferences);
                        setQuestion(
                            nextQuestion(initialPreferences, entryPoints)
                        );
                    }}
                >
                    <span className="fas fa-redo"></span>{' '}
                    <span>Start again</span>
                </button>
            </div>
        );

    const [focusTrap, setFocusTrap] = useState({});

    let modalRef = null,
        modalContentRef = null;
    const getModal = () => modalRef,
        getModalContent = () => modalContentRef;

    useEffect(() => {
        if (!focusTrap.activate) {
            setFocusTrap(
                createFocusTrap(getModalContent(), {
                    initialFocus: getModalContent().querySelector(
                        '.how-to-apply--pgt--js__options button, .how-to-apply--pgt--js__options a'
                    ),
                    onDeactivate: () => setModalVisible(false),
                    clickOutsideDeactivates: true,
                })
            );
        }

        if (modalVisible) {
            focusTrap.activate && focusTrap.activate();
            disableBodyScroll(getModal());
        } else {
            focusTrap.deactivate && focusTrap.deactivate();
            enableBodyScroll(getModal());
        }
    }, [modalVisible]);

    return (
        <>
            <ul className="cta-block">
                <li className="cta-block__cta">
                    <button
                        className="primary-cta-arrow--bright"
                        onClick={() => setModalVisible(true)}
                    >
                        <span>Apply now</span>
                    </button>
                </li>
            </ul>
            <div
                className="how-to-apply--pgt--js__modal"
                data-open={modalVisible}
                ref={(modal) => (modalRef = modal)}
            >
                <div
                    className="how-to-apply--pgt--js__modal__content"
                    ref={(modalContent) => (modalContentRef = modalContent)}
                >
                    <button
                        className="how-to-apply--pgt--js__close"
                        onClick={() => {
                            focusTrap.deactivate && focusTrap.deactivate();
                            enableBodyScroll(getModal());
                            setModalVisible(false);
                        }}
                    >
                        <span
                            className="fas fa-times icon"
                            aria-label="Close"
                        ></span>
                    </button>
                    {reset}
                    <ol className="how-to-apply--pgt--js__modal__progress">
                        {qualificationsProgress}
                        {subjectsProgress}
                        {entryPointsProgress}
                        {locationsProgress}
                        {nextStep}
                    </ol>
                    <ol className="how-to-apply--pgt--js__modal__progress--mobile">
                        {qualificationsProgressMobile}
                        {subjectsProgressMobile}
                        {entryPointsProgressMobile}
                        {locationsProgressMobile}
                        {nextStepMobile}
                    </ol>
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        <h2>Start in {props.element.dataset.year}</h2>
                    </div>
                    {qualificationQuestion}
                    {subjectQuestion}
                    {entryQuestion}
                    {locationQuestion}
                    {applyQuestion}
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        {alternative}
                    </div>
                </div>
            </div>
        </>
    );
}

const nextLevel = (arr, k) => arr.filter((i) => Object.keys(i)[0] === k)[0][k];

/**
 * Condense the configuration options for easier access.
 *
 * @param {Object[]} config The JSONified entry points configuration.
 * @return {Object[]} - The simplified configuration
 */
function distilConfiguration(config) {
    return config.map((qualification) => {
        const qualificationHeader = qualification.header,
            subjects = qualification.options.map((subject) => {
                const subjectHeader = subject.header,
                    entries = subject.options.map((entry) => {
                        const entryHeader = entry.header,
                            locations = entry.options.map((location) => {
                                const locationHeader = location.header,
                                    methods = location.options.map((method) => {
                                        const methodHeader = method.header,
                                            applyLink = method.options.apply,
                                            methodObj = new Object();

                                        if (methodHeader) {
                                            methodObj[methodHeader] = applyLink;
                                        } else {
                                            methodObj['UNIQUE'] = applyLink;
                                        }
                                        return methodObj;
                                    }),
                                    locationObject = new Object();

                                if (locationHeader) {
                                    locationObject[locationHeader] = methods;
                                } else {
                                    locationObject['UNIQUE'] = methods;
                                }

                                return locationObject;
                            }),
                            entryObj = new Object();

                        if (entryHeader) {
                            entryObj[entryHeader] = locations;
                        } else {
                            entryObj['UNIQUE'] = locations;
                        }
                        return entryObj;
                    }),
                    subjectObj = new Object();

                if (subjectHeader) {
                    subjectObj[subjectHeader] = entries;
                } else {
                    subjectObj['UNIQUE'] = entries;
                }
                return subjectObj;
            }),
            qualificationObj = new Object();

        if (qualificationHeader) {
            qualificationObj[qualificationHeader] = subjects;
        } else {
            qualificationObj['UNIQUE'] = subjects;
        }
        return qualificationObj;
    });
}

// Function takes in preferences (defined in state) on initial load and every subsequent click
function nextQuestion(preferences) {
    const question =
        preferences.qualification === null
            ? 'qualification'
            : preferences.subject === null
            ? 'subject'
            : preferences.entry === null
            ? 'entry'
            : preferences.location === null
            ? 'location'
            : 'apply';

    return question;
}

HowToApply.propTypes = {
    config: PropTypes.array,
    element: PropTypes.object,
};

export default HowToApply;
